form {
  width: 45vw;
  margin: 0 auto;
  height: 100vh;
  color: white;
  /* display: flex;
  justify-content: center; */
  overflow: auto;
  background-color: rgba(0, 0, 0, 0.6);
}

form h1 {
  text-align: center;
  margin-top: 10vh;
  font-size: 3rem;
}
.credentials {
  /* background-color: red; */
  display: flex;
  justify-content: center;
}

.c-wrap {
  /* background-color: blue; */
  height: 70vh;
  padding-top: 10vh;
}

.c-wrap label {
  display: block;
  font-size: 20px;
}

.c-wrap input {
  width: 20vw;
  height: 5vh;
  margin-bottom: 20px;
}

.c-wrap button {
  width: 4vw;
  height: 4vh;
  border: none;
  border-radius: 2px;
  cursor: pointer;
  display: block;
}

.c-wrap button:hover {
  background-color: #3895d3;
  color: white;
}

.c-wrap img {
  margin-top: 5vh;
  width: 15vw;
  border: 1px solid white;
}
