@import url("https://fonts.googleapis.com/css2?family=Dosis:wght@500&display=swap");

* {
  margin: 0;
  padding: 0;
  text-decoration: none;
  font-family: Dosis;
  box-sizing: border-box;
  scrollbar-width: none;
  -ms-overflow-style: none;
  scroll-behavior: smooth;
  transition: all 0.3s;
}
::-webkit-scrollbar {
  display: none;
}

body {
  background-image: url(../picture/witch_magic.jpg);
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: cover;
}

.loader {
  margin: 20px;
}

.nav {
  background-color: rgba(0, 0, 0, 0.7);
  width: 100%;
  position: fixed;
  z-index: 10;
  overflow: hidden;
  top: 0;
}

.header-height {
  height: 55px;
}

.center {
  display: inline-flex;
  justify-content: center;
  align-items: center;
}

.head-wrapper {
  width: 80%;
  margin: 0 auto;
}

.title-wrapper {
  background-color: rgba(44, 44, 44, 0.5);
  color: white;
  width: 10vw;
  font-family: Karla;
  letter-spacing: 2px;
}

.nav-link {
  color: #f4f4f9;
  padding: 0 20px;
  vertical-align: middle;
  float: right;
  transition: color 0.1s;
}

.nav-link:hover {
  color: #3895d3;
}

.panel {
  background-color: rgba(0, 0, 0, 0.7);
  width: 80%;
  margin: 0 auto;
  padding-top: 100px;
  box-shadow: 2px 2px 10px 1px rgb(0, 0, 0);
}

.jump {
  position: fixed;
  bottom: 8vh;
  right: 16vw;
  width: 40px;
  height: 30px;
  background-color: rgba(255, 255, 255, 0.5);
  border-radius: 3px;
  transition: all 0.2s;
}

.jump img {
  width: 100%;
  height: 100%;
  cursor: pointer;
}

.jump:hover {
  background-color: rgba(255, 255, 255, 1);
}
