.banner {
  text-align: center;
  width: 65%;
  height: 10%;
  margin: 0 auto 8vh auto;
}

.banner img {
  height: 100%;
  width: 100%;
  border: 1px solid white;
}

.anime-wrapper {
  margin: 0 auto;
  width: 60%;
  margin-top: 30px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.poster {
  display: block;
  width: 33%;
  height: auto;
  padding: 17px;
}

.content-wrapper {
  width: 100%;
  height: 100%;
  position: relative;
  border: 2px solid white;
}

.content-wrapper img {
  width: 100%;
  height: 100%;
  z-index: 0;
  transition: filter 0.2s;
}

.content-wrapper:hover img {
  filter: blur(0) brightness(0.5);
}

.anime-info {
  display: flex;
  align-content: center;
  justify-content: center;
  color: white;
  font-size: 0.7rem;
  background-color: rgba(0, 0, 0, 0.7);
  text-align: center;
  position: absolute;
  padding: 4px;
  top: 80%;
  right: 0;
  left: 0;
  bottom: 0;
  z-index: 2;
}

.cross-box {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  width: 30px;
  height: 30px;
  left: 86%;
  z-index: 3;
  border-bottom-left-radius: 2px;
  /* background: red; */
  cursor: pointer;
}
.spoiler {
  width: 50%;
  height: 50%;
  z-index: 4;
  /* background-color: black; */
}
.cross {
  color: #fff;
  font-family: Quicksand;
  width: 100%;
  height: 100%;
  opacity: 0;
  transition: opacity 0.2s;
}

.spoiler:hover div {
  opacity: 1;
}

@media (max-width: 1400px) {
  .poster {
    width: 50%;
    height: auto;
    padding: 25px;
  }
}

@media (max-width: 900px) {
  .poster {
    padding: 20px;
  }
}

@media (max-width: 700px) {
  .poster {
    width: 100%;
    padding: 15px;
  }
}
