.top-header {
  color: white;
  font-size: 35px;
  text-align: center;
  margin-top: 40px;
}
.top-poster {
  padding: 30px !important;
}
.rank-box {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  z-index: 3;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
}

.rank {
  color: #fff;
  opacity: 0;
  font-size: 70px;
  z-index: 4;
  transition: opacity 0.2s;
}

.rank-box:hover div {
  opacity: 0.8;
}
